define(['jquery'], function ($) {

    /**
     * Insert the header.
     *
     * @param {string} sfile
     * @returns void
     */
    function insertHeader(sfile) {
        $('<img src="' + sfile + '" style="height:auto; width:100%; object-fit: fill;">').prependTo("#course-header");
        $("header#page-header div.w-100 div.page-context-header").css({
            "position": "relative",
            "top": "-80px",
            "left": "50px",
            "margin-bottom": "-40px"
        });
        $("header#page-header div.w-100 h1").css({
            "color": "white"
        });
        $("div#page div#topofscroll").css("padding-top", "0px");
    }

    return {
        init: function (sfile) {
            insertHeader(sfile);
        }
    };
});